"use strict";

import Processor from "./Processor";

import Set_ from "./operations/Set";
import ArithmeticPath from './paths/ArithmeticPath'

export default class ProcessorFactory {

    constructor() {

    }

    getOperation(configuration) {
        return new Set_(configuration, this);
    }

    getPath(configuration) {
        return new ArithmeticPath(configuration);
    }

    getProcessor(configuration) {
        return new Processor(configuration, this);
    }

}
